.contact{
    background-color: #749aaac8;
    display: flex;

    .left{
        flex: 1;
        overflow: hidden;
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
        }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                width: 300px;
                height: 40px;
                font-size: 14px;
            }

            textarea{
                width: 300px;
                height: 200px;
                font-size: 14px;
            }

            button{
                width: 150px;
                height: 30px;
                color: antiquewhite;
                background-color: #465962fa;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus{
                    outline: none;
                }
            }

            p{
                color: aquamarine;
            }
        }
    }
}